import React from "react";
import axios from 'axios';
import Price from "./price"

class PricesList extends React.Component {

    constructor(props) {
        super(props)

        this.state = {
            prices: []
        }
    }

    getPrices() {
        axios.get("https://dolarhoy.herokuapp.com/dolar").then(response => {
            console.log(response.data)

            this.setState({ prices: response.data.precios })
        })
            .catch(e => {
                console.log(e)
            })
    }

    componentDidMount() {
        this.getPrices()
    }


    render() {
        return this.state.prices.map(price => {
            return (
                <Price key={price.nombre} name={price.nombre} price={price.precio} />
            )
        })
    }
}

export default PricesList;