import React from "react";
import Chart from "react-chartjs"
import moment from "moment"
import axios from "axios"
const LineChart = Chart.Line;


const chartOptions = {
    ///Boolean - Whether grid lines are shown across the chart
    scaleShowGridLines: true,

    //String - Colour of the grid lines
    scaleGridLineColor: "rgba(0,0,0,.05)",

    //Number - Width of the grid lines
    scaleGridLineWidth: 1,

    //Boolean - Whether to show horizontal lines (except X axis)
    scaleShowHorizontalLines: true,

    //Boolean - Whether to show vertical lines (except Y axis)
    scaleShowVerticalLines: true,

    //Boolean - Whether the line is curved between points
    bezierCurve: true,

    //Number - Tension of the bezier curve between points
    bezierCurveTension: 0.4,

    //Boolean - Whether to show a dot for each point
    pointDot: true,

    //Number - Radius of each point dot in pixels
    pointDotRadius: 4,

    //Number - Pixel width of point dot stroke
    pointDotStrokeWidth: 1,

    //Number - amount extra to add to the radius to cater for hit detection outside the drawn point
    pointHitDetectionRadius: 20,

    //Boolean - Whether to show a stroke for datasets
    datasetStroke: true,

    //Number - Pixel width of dataset stroke
    datasetStrokeWidth: 2,

    //Boolean - Whether to fill the dataset with a colour
    datasetFill: true,

    //Boolean - Whether to horizontally center the label and point dot inside the grid
    offsetGridLines: false
}

class PricesChart extends React.Component {

    constructor(props) {
        super(props)

        this.state = {
            labels: [],
            data: []
        }
    }

    componentDidMount() {
        this.getHistory()
    }

    getHistory() {
        let myThis = this;
        axios.get("https://dolarhoy.herokuapp.com/history/120").then(response => {
            let data = response.data
            var dt = []
            var simadi = []
            var date = []
            var i = 0
            data.forEach(item => {
                if (!(i % 7)) {
                    dt.push(item.dt);
                    simadi.push(item.simadi);
                    date.push(moment(item.date * 1000).format('MM-DD-YYYY'))
                }
                i = i + 1;

            });
            myThis.setState({
                labels: date,
                data: [dt, simadi]
            })
        })
            .catch(e => {
                console.log(e)
            })
    }

    render() {
        let { labels, data } = this.state
        var chartData = {
            labels,
            datasets: [
                {
                    label: "Dolar Today",
                    data: data[0],
                    fillColor: "#1B5E20"
                },
                {
                    label: "Simadi",
                    data: data[1],
                    fillColor: "#43A047"
                }
            ]
        }
        console.log(chartData)
        return (<LineChart data={chartData} options={chartOptions} width="600" height="250" redraw />)
    }
}


module.exports = PricesChart